$grey: #777;
$orange: #f87311;

.list-view{
    margin: 37px auto; 
    h1{
        font-family: 'Montserrat', sans-serif !important;
        font-weight: bold;
        text-transform: uppercase;
    }
    .list-view-item{
        margin: 20px auto;
    }
    .title{
        text-transform: uppercase;
    }
    .metadata{
        color: $grey;
    }

    .find-out-more {
        a {
            color: #ff0200;
            &:hover {
                color: #f87311;
            }
        }
    }

    .news-tags{
        width: 100%;
        padding-top: 10px !important;
        padding-bottom: 10px !important;
        border-top: 1px solid #f3f3f3;
        margin-top: 30px;
        ul{
            margin-bottom: 0 !important;
            li{
                display: inline-block;
                padding: 1px 7px 1px 18px;
                background: #f87311;
                margin-right: 5px;
                position: relative;
                margin-bottom: 5px;
                a{
                    color: white;
                    line-height: 20px;
                    font-size: 12px;
                }
            }
            :before{
                content: "";
                position: absolute;
                height: 0;
                width: 0;
                border-top: 16px solid white;
                border-right: 16px solid transparent;
                top: 0;
                left: 0;
            }
            :after{
                content: "";
                position: absolute;
                height: 0;
                width: 0;
                border-bottom: 16px solid white;
                border-right: 16px solid transparent;
                bottom: 0;
                left: 0;
            }
            li:hover{
                background: #ff0200;
            }
        }
    }
}

.pagination{
    width: 100%;
    ul{
        width: 100%;
        text-align: center;
        
        li{
            width: 40px;
            height: 40px;
            display: inline-block;
            line-height: 40px;
            text-align: center;
            cursor: pointer;
            background: #e8e9ea;
            border-radius: 50%;
            margin: 0 15px;
            
        }
        li.number{
            display: none;
        }
        li.active, li:hover{
            background: $orange;
            a {
                color: white;
            }
        }
        li.direction{
            visibility: hidden;
            display: inline-block
        }
        li.direction i{
            visibility: hidden;
        }
        li.appear{
            display: inline-block!important;
            visibility: visible;
        }
        li.appear i{
            visibility: visible;    
        }
    }
}//.pagination